import React, { Component } from 'react'
import TextList from '../text-list/text-list'
import ItemAddForm from '../item-add-form/item-add-form'

export default class TextContentPage extends Component {

    maxId = 100

    state = {
        post: '',
        textData: [
            this.createTextItem('first post'),
            this.createTextItem('second post'),
        ]
    }

    createTextItem(post) {
        return {
            post,
            id: this.maxId++
        }
    }

    addItem = (text) => {
        const newItem = this.createTodoItem(text)
        this.setState(({ todoData }) => {
            const newArr = [
                ...todoData,
                newItem
            ]

            return {
                todoData: newArr
            }
        })
    }

    onLabelChange = (e) => {
        this.setState({
            label: e.target.value
        })
    }

    onSubmit = (e) => {
        e.preventDefault()
        this.props.onItemAdded(this.state.label)
        this.setState({
            label: ''
        })
    }

    // onChangePage = () => {
    //     this.props.history.push('/')
    // }

    render() {

        const { textData } = this.state

        return (
            <div>
                <ItemAddForm onItemAdded={this.addItem} />
                <TextList textData={textData} />
            </div>

        )
    }
}